var Pieces=[{"id":1,"name":"Empower","thumb":"/img/Empower-thumb.jpg","previewImage":"/img/Empower-Preview.jpg","previewBlurb":"Using data science to identify home energy inefficiences and available solutions","previewCTA":"View project details","details":{"hero":"/img/Empower-DetailsHero.jpg","herosmall":"/img/Empower-DetailsHero-sm.jpg","about":"Home energy inefficiencies cost homeowners hundreds to thousands of dollars per year, on top of their energy utility bills. Empower uses sophisticated algorithms to analyze utility and public data to sniff out where a home is losing its energy.","sections":[{"tag":"large","p":"By entering in your home’s address, Empower accesses data from home energy providers, county auditors, and NOAA.","img":"/img/Empower-Feature-Large-1.jpg","imgsmall":"/img/Empower-Feature-Large-1-sm.jpg"},{"tag":"large","p":"Data science provides insight into your home’s energy use and offers products and services to mitigate the issues.","img":"/img/Empower-Feature-Large-2.jpg","imgsmall":"/img/Empower-Feature-Large-2-sm.jpg"},{"tag":"text","headline":"My Involvement","copy":"I acted as Lead Designer on the product overhaul and re-architected the experience by streamlining the user flow and providing the right information at the right time for homeowners."}]}},{"id":2,"name":"SilentNight","thumb":"/img/Silentnight-thumb.jpg","previewImage":"/img/Silentnight-Preview.jpg","previewBlurb":"Helping the UK’s leading mattress manufacturer transition into direct selling.","previewCTA":"View project details","details":{"hero":"/img/Silentnight-DetailsHero.jpg","herosmall":"/img/Silentnight-DetailsHero-sm.jpg","about":"Despite dominance of market share—by a considerable margin—Silentnight was feeling the pressure from new direct-to-consumer options like Casper and Leesa. They needed help to ditch the manufacturer paradigm and begin selling directly to buyers.","sections":[{"tag":"large","p":"","img":"/img/Silentnight-Feature-Large.jpg","imgsmall":"/img/Silentnight-Feature-Large-sm.jpg"},{"tag":"text","headline":"My Involvement","copy":"In conjunction with a new digital marketing plan and media push, I designed a commerce-enabled landing page (option B above) to be tested against their re-designed e-commerce site (option A). By partnering with SEO, Media, and Data Analytics expertise, we were able to outperform their e-commerce site by a staggering percentage by surfacing desirable content that lent itself to online transactions.","result":">1,200%","caption":"Conversion rate over option A"}]}},{"id":3,"name":"My Wendy’s","thumb":"/img/MyWendys-thumb.jpg","previewImage":"/img/MyWendys-Preview.jpg","previewBlurb":"The QSR Category’s first nationally-available mobile payment app","previewCTA":"View project details","details":{"hero":"/img/MyWendys-DetailsHero.jpg","herosmall":"/img/MyWendys-DetailsHero-sm.jpg","about":"Just prior to the lunch hour, wendys.com saw traffic spiking to their nutrition information pages. The MyWendy’s app was born from this insight to make that information accessible for those standing in or going through the drive-thru.","sections":[{"tag":"columns","img1":"/img/MyWendys-MultiColumns-1.png","img2":"/img/MyWendys-MultiColumns-2.png","img3":"/img/MyWendys-MultiColumns-3.png","caption1":"Set","caption2":"Choose","caption3":"Personalize","copy1":"Find meals that met calorie demands","copy2":"Select from meal options and choose alternative side items","copy3":"Choose ingredients you want added or substracted from individual items"},{"tag":"large","p":"After field tests and pilots in two local markets, Wendy’s became the first QSR to launch mobile payment nationwide.","img":"/img/MyWendys-Pay.jpg","imgsmall":"/img/MyWendys-Pay-sm.jpg"},{"tag":"medium","img":"/img/MyWendys-Personalization.jpg","p":"The MyWendy’s experience was integrated with wendys.com so the meals could be created anywhere and also set the stage for future ordering functionality."},{"tag":"text","headline":"My Involvement","copy":"I collaborated with Creative Direction and an iOS developer to prototype the nutrition path used to pitch the client. After officially being awarded the work, I provided the experience architecture and Art Direction, while closely collaborating with technology during the production of the first release. For subsequent releases, more staff was brought on to the project at which point I focused solely on Art Direction and User Interface design until the app was taken in-house."}]}},{"id":4,"name":"Barnes&Noble","thumb":"/img/BN-thumb.jpg","previewImage":"/img/BN-Preview.jpg","previewBlurb":"Lorem ipsum dolor sit amet adipiscing elit consectetur","previewCTA":"View project details","details":{"hero":"/img/BN-DetailsHero.jpg","herosmall":"/img/BN-DetailsHero-sm.jpg","about":"Like many retailers, Barnes&Noble was struggling to keep pace with the likes of Amazon and other large-scale e-tailers.","sections":[{"tag":"text","headline":"The Opportunity","copy":"Trying to out-Amazon Amazon seemed like an effort that would be destined for failure from the outset. So while Amazon could compete on price, inventory, and shipping, customers showed that there was much more to it than just transactions and the act of reading, itself. Passionate readers enjoy the quest for their next book just as much as being enveloped in the stories they consume. "},{"tag":"large","p":"More than an e-commerce redesign, content was used to help readers uncover the books they didn't even know they yet wanted to read.","img":"/img/BN-Large-Feature-2.jpg","imgsmall":"/img/BN-Large-Feature-2-sm.jpg"},{"tag":"columns","img1":"/img/BN-Search.jpg","img2":"/img/BN-Genre.jpg","img3":"/img/BN-YourReads.jpg","caption1":"Search Content & Products","caption2":"Find Specific Genres","caption3":"Read Anywhere","copy1":"Site search targeted both discovery content and products","copy2":"Book subjects are more specific to human interests, not traditional categorization","copy3":"The reading experience should be seamless and not limited to one platform"},{"tag":"large","p":"Most samples of books are limited to front matter, not the actual story. Like one can in their bricks and mortars, we pushed to offer more accessible samples.","img":"/img/BN-Large-Feature-1.jpg","imgsmall":"/img/BN-Large-Feature-1-sm.jpg"},{"tag":"text","headline":"My Involvement","copy":"In this project, I collaborated closely with strategy (Customer and UX), technology, and the client, while overseeing a team of 4 designers and a copywriter."}]}},{"id":5,"name":"Hershey’s","thumb":"/img/Hersheys-thumb.jpg","previewImage":"/img/Hersheys-Preview.jpg","previewBlurb":"Lorem ipsum dolor sit amet adipiscing elit consectetur","previewCTA":"View project details","details":{"hero":"/img/Hersheys-DetailsHero.jpg","herosmall":"/img/Hersheys-DetailsHero-sm.jpg","about":"The Hershey’s Company were experiencing a decline in sales of their premium brands and needed help prototyping experiences that could breathe new life into their product line.","sections":[{"tag":"large","p":"A new culinary program provided an opportunity to infuse premium Hershey’s chocolates into subscription based, chef-prepared meal kits.","img":"/img/Hersheys-Large-Feature-1.jpg","imgsmall":"/img/Hersheys-Large-Feature-1-sm.jpg"},{"tag":"columns","img1":"/img/Hersheys-Experience.jpg","img2":"/img/Hersheys-Profile.jpg","img3":"/img/Hersheys-Prefs.jpg","caption1":"Subscripton Type","caption2":"Flavor Profile","caption3":"Cuisine Types","copy1":"Customers can choose between dinner, dessert, or plans for both","copy2":"Build a personal plan based on flavor preferences","copy3":"Determine how flavors come together"},{"tag":"large","p":"","img":"/img/Hersheys-Large-Feature-2.jpg","imgsmall":"/img/Hersheys-Large-Feature-2-sm.jpg"},{"tag":"text","headline":"My Involvement","copy":"Working with Creative Leadership and the client, I acted as the Lead Designer on the development of two different experiences."}]}},{"id":6,"name":"Looped Transit","thumb":"/img/Looped-thumb.jpg","previewImage":"/img/Looped-Preview.jpg","previewBlurb":"Lorem ipsum dolor sit amet adipiscing elit consectetur","previewCTA":"View project details","details":{"hero":"/img/Looped-DetailsHero.jpg","herosmall":"/img/Looped-DetailsHero-sm.jpg","about":"Before Columbus, OH was awarded Smart City by the US Department of Transportation in 2016, Looped Transit collaborated with the City on a ride-sharing program.","sections":[{"tag":"large","p":"Application Flows","img":"/img/Looped-Large-Feature.jpg","imgsmall":"/img/Looped-Large-Feature-sm.jpg"},{"tag":"large","p":"Annotated Wireframes","img":"/img/Looped-Large-Feature-2.jpg","imgsmall":"/img/Looped-Large-Feature-2-sm.jpg"},{"tag":"text","headline":"My Involvement","copy":"I provided User Experience guidance and deliverables."}]}},{"id":7,"name":"Visual Communication","thumb":"/img/VisCom-thumb.jpg","previewImage":"/img/VisCom-Preview.jpg","previewBlurb":"Lorem ipsum dolor sit amet adipiscing elit consectetur","previewCTA":"View project details","details":{"hero":"/img/VisCom-Hero.jpg","herosmall":"/img/VisCom-DetailsHero-sm.jpg","about":"Though digital has been in my blood since I first played on the internet in 1995, I was formally trained in Visual Communication Design. This training was the foundation for the visual design and information architecture that is brought to my work.","sections":[{"tag":"large","p":"","img":"/img/VisCom-Feature-Alacrity.jpg","imgsmall":"/img/VisCom-Feature-Alacrity-sm.jpg"},{"tag":"large","p":"","img":"/img/VisCom-Feature-Dre.jpg","imgsmall":"/img/VisCom-Feature-Dre-sm.jpg"},{"tag":"large","p":"","img":"/img/VisCom-Feature-Key.jpg","imgsmall":"/img/VisCom-Feature-Key-sm.jpg"},{"tag":"large","p":"","img":"/img/VisCom-Feature-Aesthetics.jpg","imgsmall":"/img/VisCom-Feature-Aesthetics-sm.jpg"}]}},{"id":8,"name":"Brand + Visual Identity","thumb":"/img/Identity-thumb.jpg","previewImage":"/img/Identity-Preview.jpg","previewBlurb":"Lorem ipsum dolor sit amet adipiscing elit consectetur","previewCTA":"View project details","details":{"hero":"/img/Identity-Hero.jpg","herosmall":"/img/Identity-DetailsHero-sm.jpg","about":"Visual identity is the cornerstone of brand communication & expression. Without it, personality is washed away in the sea of sameness. The craft of visual identity melds together two of my favorite design components, typography and precision shape making.","sections":[{"tag":"large","p":"","img":"/img/Identity-Feature-Large-3.jpg","imgsmall":"/img/Identity-Feature-Large-3-sm.jpg"},{"tag":"large","p":"","img":"/img/Identity-Feature-Large-2.jpg","imgsmall":"/img/Identity-Feature-Large-2-sm.jpg"},{"tag":"large","p":"","img":"/img/Identity-Feature-Large.jpg","imgsmall":"/img/Identity-Feature-Large-sm.jpg"}]}},{"id":9,"name":"Sketchbook","thumb":"/img/Sketchbook-thumb.jpg","previewImage":"/img/Sketchbook-Preview.jpg","previewBlurb":"Lorem ipsum dolor sit amet adipiscing elit consectetur","previewCTA":"View project details","details":{"hero":"/img/Sketchbook-Hero.jpg","herosmall":"/img/Sketchbook-DetailsHero-sm.jpg","about":"Design happens in sketchbooks, on loose leaves and napkins. The computer is simply a production tool. I sometimes get lucky and have a vision in my head where I can just go straight to my Mac, but most of the time, the genesis of ideas are started on paper.","sections":[{"tag":"large","p":"","img":"/img/Sketchbook-Feature-Large.jpg","imgsmall":"/img/Sketchbook-Feature-Large-sm.jpg"},{"tag":"large","p":"","img":"/img/Sketchbook-Feature-Large-2.jpg","imgsmall":"/img/Sketchbook-Feature-Large-2-sm.jpg"},{"tag":"medium-rev","p":"","img":"/img/Sketchbook-Feature-1.jpg"},{"tag":"medium-rev","p":"","img":"/img/Sketchbook-Feature-2.jpg"},{"tag":"medium-rev","p":"","img":"/img/Sketchbook-Feature-3.jpg"},{"tag":"medium-rev","p":"","img":"/img/Sketchbook-Feature-4.jpg"}]}}];/* harmony default export */ __webpack_exports__["a"] = (Pieces);

